.nav-bar {
  background: #181818;
  width: 60px;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 3;
  min-height: 500px;
}

.nav-bar .logo {
  display: block;
  padding: 8px 0;
  text-decoration: none;
  text-align: center;
}

.nav-bar .logo img {
  display: block;
  margin: 8px auto;
  width: 60px;
  height: auto;
}

.nav-bar .logo .logo-name {
  height: 100%;
  color: white;
  font-size: 1.2rem;
  font-weight: 1000;
  letter-spacing: 1px;
}

.nav-bar nav {
  display: block;
  text-align: center;
  position: absolute;
  height: 210px;
  top: 35%;
  margin-top: -120p;
  width: 100%;
}

.nav-bar nav a {
  font-size: 22px;
  color: #4d4d4e;
  display: block;
  line-height: 51px;
  height: 51px;
  position: relative;
  text-decoration: none;
}

.nav-bar nav a i {
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.nav-bar nav a:hover {
  color: red;
}

.nav-bar nav a:hover svg {
  opacity: 0;
}

.nav-bar nav a:hover:after {
  opacity: 1;
}

.nav-bar nav a:after {
  content: '';
  font-size: 9px;
  letter-spacing: 2px;
  font-weight: 1000;
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  text-align: center;
  opacity: 0;
  -webkit-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
}

.nav-bar nav a:first-child::after {
  content: 'HOME';
}

.nav-bar nav a.about-link:after {
  content: 'ABOUT';
}

.nav-bar nav a.contact-link:after {
  content: 'CONTACT';
}

.nav-bar nav a.portfolio-link:after {
  content: 'PORTFOLIO';
  letter-spacing: 1px;
}

.nav-bar nav a.active svg {
  color: red;
}

.nav-bar ul {
  position: absolute;
  bottom: 20px;
  width: 100%;
  display: block;
  padding: 0;
  list-style: none;
  text-align: center;
  margin: 0;
}

.nav-bar ul li a {
  display: block;
  padding: 7px 0;
  font-size: 15px;
  line-height: 16px;
}

.nav-bar ul li a:hover svg {
  color: red;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
