.home-page .text-zone {
  position: absolute;
  left: 10%;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  width: 40%;
  max-height: 90%;
}

.home-page h1 {
  color: #fff;
  font-size: 53px;
  margin: 0;
  font-family: 'Coolvetica';
  font-weight: 400;
}

.home-page h1::before {
  content: '<h1>';
  font-family: 'La Belle Aurore';
  color: #ffd700;
  font-size: 18px;
  position: absolute;
  margin-top: -40px;
  left: 15px;
  opacity: 0.6;
}

.home-page h1::after {
  content: '</h1>';
  font-family: 'La Belle Aurore';
  color: #ffd700;
  font-size: 18px;
  position: absolute;
  margin-top: 42px;
  margin-left: 20px;
  -webkit-animation: fadeIn 1s backwards;
          animation: fadeIn 1s backwards;
  opacity: 0.6;
}

.home-page h2 {
  color: #8d8d8d;
  margin-top: 20px;
  font-weight: 400;
  font-size: 11px;
  font-family: sans-serif;
  letter-spacing: 3px;
  -webkit-animation: fadeIn 1s 2.2s backwards;
          animation: fadeIn 1s 2.2s backwards;
}

.home-page .flat-button {
  color: #ffd700;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 4px;
  font-family: sans-serif;
  text-decoration: none;
  padding: 10px 10px;
  border: 1px solid #ffd700;
  margin-top: 25px;
  float: left;
  -webkit-animation: fadeIn 1s 2.2s backwards;
          animation: fadeIn 1s 2.2s backwards;
  white-space: nowrap;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.home-page .flat-button:hover {
  background: #ffd700;
  color: #333;
  -webkit-animation: swing 1s;
          animation: swing 1s;
}

.home-page model-viewer {
  position: absolute;
  left: 10%;
  top: -200px;
  height: 900px;
  width: 100vw;
  z-index: -1;
}
