.nav-bar {
    background: #181818;
    width: 60px;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 3;
    min-height: 500px;

    .logo {
        display: block;
        padding: 8px 0;
        text-decoration: none;
        text-align: center;

        img {
            display: block;
            margin: 8px auto;
            width: 60px;
            height: auto;
        }

        .logo-name {
            height: 100%;
            color: white;
            font-size: 1.2rem;
            font-weight: 1000;
            letter-spacing: 1px;
        }
    }

    nav {
        display: block;
        text-align: center;
        position: absolute;
        height: 210px;
        top: 35%;
        margin-top: -120p;
        width: 100%;

        a {
            font-size: 22px;
            color: #4d4d4e;
            display: block;
            line-height: 51px;
            height: 51px;
            position: relative;
            text-decoration: none;

            i {
                transition: all 0.3s ease-out;
            }

            &:hover {
                color: red;

                svg {
                    opacity: 0;
                }

                &:after {
                    opacity: 1;
                }
            }

            &:after {
                content: '';
                font-size: 9px;
                letter-spacing: 2px;
                font-weight: 1000;
                position: absolute;
                bottom: 0;
                display: block;
                width: 100%;
                text-align: center;
                opacity: 0;
                transition: all 0.1s ease-out;
            }

            &:first-child {
                &::after {
                    content: 'HOME';
                }
            }
        }

        a.about-link {
            &:after {
                content: 'ABOUT';
            }
        }

        a.contact-link {
            &:after {
                content: 'CONTACT';
            }
        }

        a.portfolio-link {
            &:after {
                content: 'PORTFOLIO';
                letter-spacing: 1px;
            }
        }

        a.active {
            svg {
                color: red;
            }
        }
    }

    ul {
        position: absolute;
        bottom: 20px;
        width: 100%;
        display: block;
        padding: 0;
        list-style: none;
        text-align: center;
        margin: 0;

        li {
            a {
                display: block;
                padding: 7px 0;
                font-size: 15px;
                line-height: 16px;

                &:hover svg {
                    color: red;
                    transition: all 0.3s ease-out;
                }
            }
        }
    }
}