.portfolio-page {
  padding-left: 100px;
  padding-right: 50px;
  width: calc(100% - 150px);
  position: initial;
  height: 100%;
  overflow: auto;
}

.portfolio-page h1.page-title {
  margin-left: 100px;
  margin-top: 100px;
}

.portfolio-page .images-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 15px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-bottom: 100px;
}

.portfolio-page .image-box {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 20%;
          flex: 1 1 20%;
  height: 400px;
  overflow: hidden;
  border-radius: 10px;
  max-width: calc(25% - 10px);
}

.portfolio-page .image-box .portfolio-image {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.portfolio-page .image-box .content {
  position: absolute;
  width: 100%;
  z-index: 3;
  padding: 10px 20px;
  -webkit-transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0, rgba(0, 0, 0, 0.2)), to(black));
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0, black);
  bottom: -70px;
}

.portfolio-page .image-box .title {
  margin-bottom: 0;
  margin-top: 0;
  color: #fff;
  font-size: 24px;
  font-weight: 500;
  line-height: 24px;
}

.portfolio-page .image-box .description {
  font-size: 14px;
  margin-bottom: 5px;
  color: #fff;
  font-weight: 700;
}

.portfolio-page .image-box .btn {
  margin-top: 30px;
  margin-bottom: 10px;
  padding: 0 23px;
  height: 40px;
  line-height: 34px;
  border: 2px solid #ffd700;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 1px;
  color: #fff;
  background: transparent;
  text-transform: uppercase;
  -webkit-transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  cursor: pointer;
}

.portfolio-page .image-box .btn:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  background: #ffd700;
  font-weight: 700;
  color: black;
}

.portfolio-page .image-box::after {
  content: "";
  background: -webkit-gradient(linear, left top, left bottom, from(#fff), to(black));
  background: linear-gradient(180deg, #fff, black);
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  -webkit-transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  opacity: 0;
}

.portfolio-page .image-box:hover:after {
  opacity: 0.7;
}

.portfolio-page .image-box:hover .content {
  bottom: 0;
  background: transparent;
}
