.contact-form {
  width: 100%;
  margin-top: 20px;
}

.contact-form ul {
  padding: 0;
  margin: 0;
}

.contact-form ul li {
  padding: 0;
  margin: 0;
  list-style: none;
  margin-bottom: 10px;
  display: block;
  position: relative;
  opacity: 0;
  -webkit-animation: fadeInUp 2s 2s;
          animation: fadeInUp 2s 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  clear: both;
}

.contact-form ul li.half {
  width: 49%;
  margin-left: 2%;
  float: left;
  clear: none;
}

.contact-form ul li.half:first-child {
  margin-left: 0;
}

.contact-form input[type='text'],
.contact-form input[type='email'] {
  width: 100%;
  border: 0;
  background: #181818;
  height: 50px;
  font-size: 16px;
  font-weight: 1000;
  color: #fff;
  padding: 0 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.contact-form textarea {
  width: 100%;
  border: 0;
  background: #181818;
  height: 50px;
  font-size: 16px;
  font-weight: 1000;
  color: #fff;
  padding: 20px;
  min-height: 150px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.contact-form .flat-button {
  color: #ffd700;
  background: 0 0;
  font-weight: 1000;
  letter-spacing: 3px;
  text-decoration: none;
  padding: 8px 10px;
  border: 1px solid #ffd700;
  float: right;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.contact-form .flat-button:hover {
  background: #ffd700;
  color: #181818;
  cursor: pointer;
  -webkit-animation: tada 1s;
          animation: tada 1s;
}

.info-map {
  position: absolute;
  background-color: #181818;
  top: 50px;
  right: 30%;
  z-index: 999999;
  width: 267px;
  padding: 20px;
  color: #fff;
  font-family: 'Helvetica';
  font-size: 17px;
  font-weight: 300;
  opacity: 0;
  -webkit-animation: fadeIn 1s 1.5s;
          animation: fadeIn 1s 1.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.info-map span {
  font-size: 16px;
  display: block;
  padding-top: 20px;
  color: #ffd700;
}

.map-wrapper {
  background: rgba(8, 253, 216, 0.1);
  float: right;
  width: 53%;
  height: 100%;
}

.map-wrapper .popup-text {
  font-size: 1.4rem;
  font-weight: 900;
}

.leaflet-container {
  width: 100%;
  height: 100%;
  position: relative;
  opacity: 0;
  -webkit-animation: backInRight 1s 1.2s;
          animation: backInRight 1s 1.2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
